
import Cuadrado from './Cuadrado'
import { useState } from 'react'

const Tablero = () => {

    const [tablero, settablero] = useState(new Array(9).fill('‚ùì'))

    const [turnoX, setTurnoX] = useState(true);



    const handleClick = (indice) => {

        if (tablero[indice] !== '‚ùì' || ganador) {
            return;
        }
        const nuevoTablero = [...tablero]
        nuevoTablero[indice] = turnoX ? 'üéÉ' : 'üéÑ';
        settablero(nuevoTablero);
        setTurnoX(!turnoX);


    }


    const renderizarCuadrado = (indice) => {
        return <Cuadrado value={tablero[indice]} onClick={() => handleClick(indice)} />

    }




    let status;
    const ganador = CalcularGanador(tablero)
    status = ganador ? `ElGanador es ${ganador}` : (tablero.filter(elem => elem === '‚ùì').length === 0) ? 'Hay un EmpateüòÆ' : turnoX ? 'Es el turno de üéÉ' : 'Es el turno de üéÑ'

    function CalcularGanador(tablero) {

        const LineasGanadoras =
            [[0, 1, 2],
            [3, 4, 5,],
            [6, 7, 8,],
            [0, 3, 6],
            [1, 4, 7],
            [2, 5, 8],
            [0, 4, 8],
            [2, 4, 6]



            ]

        for (let i = 0; i < LineasGanadoras.length; i++) {

            const [a, b, c] = LineasGanadoras[i]
            if ((tablero[a] !== '‚ùì') && tablero[a] === tablero[b] && tablero[b] === tablero[c]) {
                return tablero[a]
            }

        }
        return null;
    }



    return (<>
        <div className="container">
            <p>{status}</p>
            <div className="row">
                <div>
                    <Cuadrado value={tablero[0]} onClick={() => handleClick(0)} />

                </div>
                <div>
                    {renderizarCuadrado(1)}
                </div>
                <div>
                    {renderizarCuadrado(2)}
                </div>
            </div>
            <div className="row">
                <div>
                    {renderizarCuadrado(3)}


                </div>
                <div>
                    {renderizarCuadrado(4)}
                </div>
                <div>
                    {renderizarCuadrado(5)}
                </div>


            </div>
            <div className="row">
                <div>
                    {renderizarCuadrado(6)}

                </div>
                <div>
                    {renderizarCuadrado(7)}

                </div>
                <div>
                    {renderizarCuadrado(8)}

                </div>


            </div>

        </div>

    </>)
}

export default Tablero;